import { UIHelp } from "../../../../frame/scripts/Utils/UIHelp";
import { EditorManager } from "../../Manager/EditorManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class TeacherItem extends cc.Component {

    @property(cc.EditBox)
    private EditBox0: cc.EditBox = null;
    @property(cc.EditBox)
    private EditBox1: cc.EditBox = null;
    @property(cc.EditBox)
    private EditBox2: cc.EditBox = null;


    private _data: any = null
    private _index: any = null

    // private tempData = {
    //     kapaiId: "", wenben: "", guanlianId: ""
    // }
    public initItem(index, data: any) {
        this._data = data;
        this._index = index;
        this.EditBox0.string = data[index].kapaiId;
        this.EditBox1.string = data[index].wenben;
        this.EditBox2.string = data[index].guanlianId;
    }


    private editingChanged_0() {
        let index = this.EditBox0.string.indexOf("\n")
        if (index != -1) {
            this.EditBox0.string = this.EditBox0.string.replace('\n', '');
            this.EditBox0.blur();
        }
    }

    private editingDidEnded_0() {
        if (this.EditBox0.string.length == 0) {
            this.EditBox0.string = "";
        } else {
            let arr = ["101", "102", "201", "202", "301", "302", "401", "402", "501", "502", "601", "602", "701", "702", "801", "802"];
            if (arr.indexOf(this.EditBox0.string) == -1) {
                UIHelp.showTip("卡牌ID错误");
                this.EditBox0.string = "";
            }
        }

        this._data[this._index].kapaiId = this.EditBox0.string;
        console.log("===============", this._data);

    }

    private editingChanged_1() {
        let index = this.EditBox1.string.indexOf("\n")
        if (index != -1) {
            this.EditBox1.string = this.EditBox1.string.replace('\n', '');
            this.EditBox1.blur();
        }
    }

    private editingDidEnded_1() {
        if (this.EditBox1.string.length == 0) {
            this.EditBox1.string = "";
        }
        this._data[this._index].wenben = this.EditBox1.string;
        console.log("===============", this._data);
    }

    private editingChanged_2() {
        let index = this.EditBox2.string.indexOf("\n")
        if (index != -1) {
            this.EditBox2.string = this.EditBox2.string.replace('\n', '');
            this.EditBox2.blur();
        }
    }

    private editingDidEnded_2() {
        if (this.EditBox2.string.length == 0) {
            this.EditBox2.string = "";
        }
        this._data[this._index].guanlianId = this.EditBox2.string;
        console.log("===============", this._data);
    }


}
